export default "/*\n\nIntro:\n\n    We have asynchronous functions now, advanced technology.\n    This makes us a tech startup officially now.\n    But one of the consultants spoiled our dreams about\n    inevitable future IT leadership.\n    He said that callback-based asynchronicity is not\n    popular anymore and everyone should use Promises.\n    He promised that if we switch to Promises, this would\n    bring promising results.\n\nExercise:\n\n    We don't want to reimplement all the data-requesting\n    functions. Let's decorate the old callback-based\n    functions with the new Promise-compatible result.\n    The final function should return a Promise which\n    would resolve with the final data directly\n    (i.e. users or admins) or would reject with an error\n    (or type Error).\n\n    The function should be named promisify.\n\n*/\n\ninterface User {\n    type: 'user';\n    name: string;\n    age: number;\n    occupation: string;\n}\n\ninterface Admin {\n    type: 'admin';\n    name: string;\n    age: number;\n    role: string;\n}\n\ntype Person = User | Admin;\n\nconst admins: Admin[] = [\n    { type: 'admin', name: 'Jane Doe', age: 32, role: 'Administrator' },\n    { type: 'admin', name: 'Bruce Willis', age: 64, role: 'World saver' }\n];\n\nconst users: User[] = [\n    { type: 'user', name: 'Max Mustermann', age: 25, occupation: 'Chimney sweep' },\n    { type: 'user', name: 'Kate Müller', age: 23, occupation: 'Astronaut' }\n];\n\nexport type ApiResponse<T> = (\n    {\n        status: 'success';\n        data: T;\n    } |\n    {\n        status: 'error';\n        error: string;\n    }\n);\n\nexport function promisify(arg: unknown): unknown {\n    return null;\n}\n\nconst oldApi = {\n    requestAdmins(callback: (response: ApiResponse<Admin[]>) => void) {\n        callback({\n            status: 'success',\n            data: admins\n        });\n    },\n    requestUsers(callback: (response: ApiResponse<User[]>) => void) {\n        callback({\n            status: 'success',\n            data: users\n        });\n    },\n    requestCurrentServerTime(callback: (response: ApiResponse<number>) => void) {\n        callback({\n            status: 'success',\n            data: Date.now()\n        });\n    },\n    requestCoffeeMachineQueueLength(callback: (response: ApiResponse<number>) => void) {\n        callback({\n            status: 'error',\n            error: 'Numeric value has exceeded Number.MAX_SAFE_INTEGER.'\n        });\n    }\n};\n\nexport const api = {\n    requestAdmins: promisify(oldApi.requestAdmins),\n    requestUsers: promisify(oldApi.requestUsers),\n    requestCurrentServerTime: promisify(oldApi.requestCurrentServerTime),\n    requestCoffeeMachineQueueLength: promisify(oldApi.requestCoffeeMachineQueueLength)\n};\n\nfunction logPerson(person: Person) {\n    console.log(\n        ` - ${person.name}, ${person.age}, ${person.type === 'admin' ? person.role : person.occupation}`\n    );\n}\n\nasync function startTheApp() {\n    console.log('Admins:');\n    (await api.requestAdmins()).forEach(logPerson);\n    console.log();\n\n    console.log('Users:');\n    (await api.requestUsers()).forEach(logPerson);\n    console.log();\n\n    console.log('Server time:');\n    console.log(`   ${new Date(await api.requestCurrentServerTime()).toLocaleString()}`);\n    console.log();\n\n    console.log('Coffee machine queue length:');\n    console.log(`   ${await api.requestCoffeeMachineQueueLength()}`);\n}\n\nstartTheApp().then(\n    () => {\n        console.log('Success!');\n    },\n    (e: Error) => {\n        console.log(`Error: \"${e.message}\", but it's fine, sometimes errors are inevitable.`);\n    }\n);\n\n// In case if you are stuck:\n// https://www.typescriptlang.org/docs/handbook/generics.html\n";